
@import url('https://fonts.googleapis.com/css2?family=Bangers&family=Bungee+Inline&family=Cutive&display=swap');


/* banner-wrappers */
.banner{
    height: 40vh;
}

.banner-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   height: 42vh;
   text-align: center;
   background-color: black;
   text-transform: uppercase;
   border-bottom: 1.5px solid orange;
}


/* banner texts */
.banner-description,
.banner-message,
.banner-title{
    color: orange;
    letter-spacing: 4px;
    font-family: 'Cutive', sans-serif;
    padding: .3em;
}

.banner-title{
    font-size: 4rem;
}


/* Mobile */
@media (max-width: 576px) { 
.banner-title{
    font-size: 1.7em;
}

.banner-description,
.banner-message
{
  font-size: 1.2rem;
  padding: .1em; 
 }

}

/* tablets */
@media (max-width: 768px) { 

    .banner-title{
        font-size: 1.5em;
    }
    
    .banner-description,
    .banner-message
    {
      font-size: 1.2rem;
      padding: .1em; 
     }

 }






