@import url('https://fonts.googleapis.com/css2?family=Bangers&family=Bungee+Inline&family=Cutive&display=swap');

/* Location CARD COMPONENT */
.cards-container{
    display: flex;
    flex-flow: column wrap;
    margin: 1em;
}

.cards{
    display:flex;
    height: 100%;
    border: 2px solid green;
   background-color: rgb(218, 209, 209);
}

.cards:hover{
    box-shadow: 5px 10px 20px 5px rgba(6, 116, 2, 0.815) !important;
}

.card-img{
   max-width: 90%;
   height: 250px;
   object-fit: cover;
   padding: 1em
}

/* zoom picture on hover */
.overflow{
    overflow: hidden;
}

.card-img-top{
    transform: scale(1);
    transition: transform 0.5s ease;
}

.card-img-top:hover{
    transform: scale(1.3);
}

/* == */

.card-title{
    font-size: 2.2rem;
    color: green;
    font-weight: 600;
    font-family: 'Cutive',sans-serif;
}

.card-text{
    font-size: 1.3rem;
    font-weight: 700;
}


/* buttons div and individual buttons */
.card-buttons{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1em 0; 
}

.locationBtn{
    margin: .5em;
    width: 80%;
    font-weight: 300;
    font-size: 1.1rem;
    padding: .5em;
}

.locationBtn:hover{
    color: orange;
}

/* ======================-> */

/* Media Queries mobile */
 @media (max-width: 576px) { 

     /* banner */
    .banner{
        height: 55vh;  
    }

    .banner-directory{
        padding-top: 3.3em;
    }

  }

/* tablet */
  @media (min-width: 768px) {
    /* banner */
    .banner{
        height: 45vh;  
    }

    .banner-directory{
        padding-top: 2em;
    }
   }





